import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import Switch from "react-switch";

import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import Loader from '../../App/components/Reusables/Loader'
import { updateDiscount, getDiscount } from '../../api/misc';
import { validateDiscountCreation } from '../../validation/discount';

const Update = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [submitting, setSubmitting] = React.useState(false)
    const [discountData, setDiscountData] = React.useState({
        id: '',
        name: '',
        type: '',
        rate: '',
        code: '',
        isActive: false,
    })


    const [errors, setErrors] = React.useState({});


    React.useEffect(() => {

        const controller = new AbortController();
        (async () => {

            await getDiscount({ action: 'get', discount_id: props.match.params.id }, controller.signal)
                .then(async (res) => {
                    console.log(res);
                    if (res.json.status) {
                        const discount = res.json.discounts[0];
                        await setDiscountData({
                            ...discountData,
                            id: discount.id,
                            name: discount.name,
                            type: discount.discount_type,
                            rate: discount.rate,
                            code: discount.code,
                            isActive: discount.is_active,
                        });
                        await setLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
        })()
        return () => controller.abort();
    }, [])


    const amendErrorOf = (field) => {
        setErrors({ ...errors, [field]: '' })
    }



    const _discountDataChange = (e) => {
        amendErrorOf(e.target.name)
        setDiscountData({
            ...discountData,
            [e.target.name]: e.target.value
        })
    }

    const _activeChange = (checked) => {
        console.log(checked);
        setDiscountData({ ...discountData, isActive: checked })
    }
    const _update = () => {

        const { isValid, errors } = validateDiscountCreation(discountData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };
        setSubmitting(true)
        //if no errros api call
        const requestJson = {
            "action": "update",
            "discount_id": discountData.id,
            "discount_type": discountData.type,
            "name": discountData.name,
            "rate": parseFloat(discountData.rate),
            "code": discountData.code,
            "is_active": discountData.isActive
        }
        console.log(requestJson);
        updateDiscount(requestJson)
            .then((res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Discount updated successfully!', background: '#dffff0' })
                    props.history.push('/discounts');
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }


    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Update Discount</h5>
                            <hr />
                            <Row>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Discount Name*</Form.Label>
                                        <Form.Control name="name" value={discountData.name} onChange={_discountDataChange} type="text" placeholder="Discount Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Discount Type*</Form.Label>
                                        <Form.Control name="type" value={discountData.type} onChange={_discountDataChange} as="select">

                                            <option value='' disabled >Select...</option>
                                            <option value='percent'  >percent</option>
                                            <option value='fixed'  >Fixed</option>
                                        </Form.Control>
                                        <ErrorBody>{errors.type}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={3}>
                                    <Form.Group  >
                                        <Form.Label>Rate*</Form.Label>
                                        <Form.Control name="rate" value={discountData.rate} onChange={_discountDataChange} type="number" placeholder="Rate" />
                                        <ErrorBody>{errors.rate}</ErrorBody>
                                    </Form.Group>
                                </Col>


                                <Col md={3}>
                                    <Form.Group  >
                                        <Form.Label>Code</Form.Label>
                                        <Form.Control name="code" value={discountData.code} onChange={_discountDataChange} type="text" placeholder="Code" />
                                        <ErrorBody>{errors.code}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group className="d-flex flex-column justify-content-end">
                                        <Form.Label style={{ marginBottom: '15px' }}>Is Active?</Form.Label>
                                        <Switch onChange={_activeChange} checked={discountData.isActive} />
                                    </Form.Group>
                                </Col>


                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/discounts') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_update}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Updating</> : 'Update'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Update;
