import isEmpty from "./isEmpty"


const validateDiscountCreation = (discountData) => {


    const errors = {}


    if (isEmpty(discountData.name)) {
        errors.name = 'Provide name of discount!'
    }
    if (isEmpty(discountData.type)) {
        errors.type = 'Provide type of discount!'
    }
    if (isEmpty(discountData.rate)) {
        errors.rate = 'Provide rate of discount!'
    }



    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

export { validateDiscountCreation }